import React, { useState, useEffect } from "react";
import styled from "styled-components";

import { firebaseapp } from "../../utils";
import "firebase/storage"

const Container = styled.div`
	display: flex;
	padding: 5vw;
	flex-direction: ${props => (props.theme.screens.md ? "column" : "row")};
	justify-content: space-around;
	align-items: center;
	background: url("/images/all_circles.svg");
`;

const TextContainer = styled.div`
	padding: 5vw;
`;

const Img = styled.img`
	width: ${props => (props.theme.screens.md ? "90%" : "25vw")};
	padding-bottom: ${props => (props.theme.screens.md ? "8vw" : "0vw")};
	height: 50vh;
	object-fit: cover;
`;

const Heading = styled.h1`
	font-family: "Open Sans", sans-serif;
	color: ${props => props.theme.colors.white};
	font-weight: 700;
	font-size: 3rem;
`;

const SubText = styled.p`
	font-family: "Crimson Text", serif;
	font-weight: 300;
	font-size: 1.5rem;
	color: ${props => props.theme.colors.white};
`;

const ImageContainer = styled.div`
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-auto-rows: auto;
	grid-gap: 1rem;
	flex: 1;
`;

const Home: React.FC = () => {
	const [img, setImg] = useState("");

	const [model1_imgs, setModel1Imgs] = useState([]);
	const [model2_imgs, setModel2Imgs] = useState([]);
	const [model3_imgs, setModel3Imgs] = useState([]);
	const [model4_imgs, setModel4Imgs] = useState([]);

	useEffect(() => {
		firebaseapp
			.storage()
			.ref("fashion_photography/models/model1")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setModel1Imgs(urls);
				});
			});

		firebaseapp
			.storage()
			.ref("fashion_photography/models/model2")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setModel2Imgs(urls);
				});
			});

		firebaseapp
			.storage()
			.ref("fashion_photography/models/model3")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setModel3Imgs(urls);
				});
			});

		firebaseapp
			.storage()
			.ref("fashion_photography/models/model4")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setModel4Imgs(urls);
				});
			});
	}, []);

	return (
		<Container>
			<TextContainer>
				<Heading>Fashion and Photography</Heading>
				<SubText>
					Talking about fashion everyone wants to be fashionable these days and want to be complimented by
					everyone. If you grow your skills towards fashion a good photo will help you to
					present your fashion to the rest of world. We have a Professional and trained team of Models,
					Designer and Photographers in order to help you with fashion and lifestyle	
				</SubText>
				<br></br>
			</TextContainer>
			<ImageContainer>
				<Img src={model2_imgs[2]}></Img>
				<Img src={model1_imgs[2]}></Img>
				<Img src={model4_imgs[0]}></Img>
				<Img src={model3_imgs[2]}></Img>
			</ImageContainer>
		</Container>
	);
};

export default Home;
